.mask {
    width: 100%;
    height: 100vh;
    position: relative;
}

.mask::after {
    content: '';
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    left: 0;
    background-color: #000;
    opacity: 0.8;
}

.into-img {
    width: 100%;
    height: 100%;
    background-size: cover;
    background-position: center;
    object-fit: cover;
}

.hero {
    width: 100%;
    height: 100%;
}

.hero .content {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;
}

.hero .content h1 {
    font-size: 4rem;
    padding: 0.6rem 1.5rem;
    margin-bottom: 20px;
}

.hero .content p {
    font-size: 1.4rem;
    font-weight: 200;
}

.hero .content .btn {
    margin: 1rem 0.2rem;
}

@media screen and (max-width: 640px) {
    .hero .content h1 {
        font-size: 3rem;
    }

}